import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { MatbottomsheetComponent } from './matbottomsheet/matbottomsheet.component';
import { MatdashboardComponent } from './matdashboard/matdashboard.component';
import { MatdialogComponent } from './matdialog/matdialog.component';
import { MatnavComponent } from './matnav/matnav.component';
import { MatsnackbarComponent } from './matsnackbar/matsnackbar.component';
import { MattableComponent } from './mattable/mattable.component';

const routes: Routes = [
  {
    path: 'matbottomsheet',
    component: MatbottomsheetComponent
  },
  {
    path: 'matsnackbar',
    component: MatsnackbarComponent
  },
  {
    path: 'matdialog',
    component: MatdialogComponent
  },
  {
    path: 'matdashboard',
    component: MatdashboardComponent
  },
  {
    path: 'matnav',
    component: MatnavComponent
  },
  {
    path: 'mattable',
    component: MattableComponent
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class MattestRoutingModule {}
